import Mock from "mockjs";
import MockData from "../mock/data.js"
import Query from "../utils/query.js"

const songInfoList = MockData.songData.songInfo;

let listSongInfo = Mock.mock('https://meta-mock/songInfo/list', 'get', () => {

    return {
        code: 200,
        success: true,
        message: '获取所有歌曲数据成功！',
        data: songInfoList
    }
});

let detailSongInfo = Mock.mock('https://meta-mock/songInfo/detail', 'post', (options) => {

    const detailSongInfoItem = JSON.parse(options.body);

    // 取对象 name 构造新数组
    let queryList = []
    songInfoList.forEach(
        function (item, index) {
            queryList.push(item.name)
        }
    )

    // 模糊查询歌曲名
    const detailSongInfoNameReturn = Query.fuzzyQuery(queryList, detailSongInfoItem.name)
    // console.log(detailSongInfoNameReturn)

    let detailSongInfoReturn = []
    // 回到原数据对象获取完整数据
    for (let index in songInfoList) {
        for (let key in detailSongInfoNameReturn) {
            if (songInfoList[index].name === detailSongInfoNameReturn[key]) {
                detailSongInfoReturn.push(songInfoList[index]);
            }
        }
    }

    return {
        code: 200,
        success: true,
        message: '获取歌曲详情成功！',
        data: detailSongInfoReturn
    }
});

let addSongInfo = Mock.mock('https://meta-mock/songInfo/add', 'post', (options) => {

    const addSongInfoItem = JSON.parse(options.body);

    // id,seq 随机生成
    const addSongDataGenerator = Mock.mock({
        'id': '@guid',
        'seq': '@increment',
    });
    addSongInfoItem.id = addSongDataGenerator.id;
    addSongInfoItem.seq = addSongDataGenerator.seq;

    songInfoList.unshift(addSongInfoItem);

    return {
        code: 200,
        success: true,
        message: '新增歌曲数据成功！',
        data: songInfoList
    }
})

let removeAllSongInfo = Mock.mock('https://meta-mock/songInfo/removeAll', 'post', (options) => {

    const removeAllSongInfoItem = JSON.parse(options.body);

    removeAllSongInfoItem.forEach((param) => {
        const findIndex = songInfoList.findIndex((item) => {
            return item.id == param;
        });
        songInfoList.splice(findIndex, 1);

    })

    return {
        code: 200,
        success: true,
        message: '批量删除歌曲数据成功',
        data: songInfoList
    }
})


let removeSongInfo = Mock.mock('https://meta-mock/songInfo/remove', 'post', (options) => {

    const removeSongInfoItem = options.body;
    // console.log(removeSongInfoItem)

    for (let index in songInfoList) {
        if (songInfoList[index].id === removeSongInfoItem) {
            songInfoList.splice(index, 1)
        }
    }

    return {
        code: 200,
        success: true,
        message: '删除歌曲数据成功',
        data: songInfoList
    }
})

let updateSongInfo = Mock.mock('https://meta-mock/songInfo/update', 'post', (options) => {

    const updateSongInfoItem = JSON.parse(options.body);

    for (let index in songInfoList) {
        if (songInfoList[index].id === updateSongInfoItem.id) {
            songInfoList[index] = updateSongInfoItem;
        }
    }

    return {
        code: 200,
        success: true,
        message: '更新歌曲数据成功',
        data: songInfoList
    }
})

export default {
    listSongInfo,
    detailSongInfo,
    addSongInfo,
    removeSongInfo,
    removeAllSongInfo,
    updateSongInfo
}
