<template>

  <span>{{ baseMsg }}</span> / <b>{{ msg }}</b>

  <div>
    <br>
    <el-tabs type="card" v-model="activeTab">

      <el-tab-pane label="搜索歌曲" name="search">
        <el-form :inline="true" :model="detailSongInfo">
          <el-form-item label="歌曲名" required>
            <el-input v-model="detailSongInfo.name" placeholder="请输入歌曲名(模糊查询)"/>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="detailItem()">搜索</el-button>
          </el-form-item>
        </el-form>
      </el-tab-pane>

      <el-tab-pane label="增加歌曲" name="add">
        <el-form :inline="true" :model="addSongInfo">
          <el-row>
            <el-col span="8">
              <el-form-item label="歌曲名" :required="true">
                <el-input v-model="addSongInfo.name" placeholder="请输入歌曲名"/>
              </el-form-item>
              <el-form-item label="歌词" :required="true">
                <el-input v-model="addSongInfo.lyric" placeholder="请输入歌词"/>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col span="8">
              <el-form-item label="热度" :required="true">
                <el-select v-model="addSongInfo.power" placeholder="选择热度">
                  <el-option
                      v-for="item in options"
                      :key="item.value"
                      :label="item.label"
                      :value="item.value"
                  />
                </el-select>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="addItem()">增加</el-button>
                <el-button type="primary" @click="clearItem()">重置</el-button>

              </el-form-item>
            </el-col>
          </el-row>
        </el-form>
      </el-tab-pane>
    </el-tabs>


    <br>
    <br>
    <hr>
    <el-table :data="listSongInfo" style="width: 100%">
      <el-table-column prop="id" label="id" width="100" v-if="false"/>
      <el-table-column prop="seq" label="序号" width="100"/>

      <el-table-column prop="name" label="歌曲名" width="200">
        <template v-slot="scope">
          <el-input type="text" v-model="scope.row.name" v-show="scope.row.isEdit"/>
          <span v-show="!scope.row.isEdit">{{ scope.row.name }}</span>
        </template>
      </el-table-column>

      <el-table-column prop="lyric" label="歌词" width="500">
        <template v-slot="scope">
          <el-input type="textarea" v-model="scope.row.lyric" v-show="scope.row.isEdit"/>
          <span v-show="!scope.row.isEdit">{{ scope.row.lyric }}</span>
        </template>
      </el-table-column>

      <el-table-column prop="power" label="热度" width="100"/>

      <el-table-column fixed="right" label="操作" width="200">
        <template #default="scope">
          <el-button
              round
              type="primary"
              size="small"
              @click.prevent="editItem(scope)"
              v-show="!scope.row.isEdit"
          >
            编辑
          </el-button>
          <el-button
              round
              type="success"
              size="small"
              @click.prevent="updateItem(scope)"
              v-show="scope.row.isEdit"
          >
            更新

          </el-button>
          <el-button
              round
              type="danger"
              size="small"
              @click.prevent="removeItem(scope)"
          >
            删除
          </el-button>
        </template>
      </el-table-column>
    </el-table>


  </div>

</template>

<script>


import SongApi from '../api/song.js';
import Promise from '../utils/promise.js';
import Options from '../constants/select.js'

export default {
  name: "SongView",
  props: {
    msg: String,
    baseMsg: String
  },
  data() {
    return {

      options: Options.songPowerOptions,
      activeTab: "search",
      listSongInfo: [],
      detailSongInfo: {},
      addSongInfo: {
        name: "",
        lyric: "",
        power: ""
      }
    }
  },
  mounted() {

    Promise.ajax({
      "type": "get",
      "url": "https://meta-mock/songInfo/list",
      "data": ""
    }).then(res => {
      // console.log(res.data);
      this.listSongInfo = res.data;

    })

  },
  methods: {
    addItem: function () {

      // 新增数据简单校验
      // if (Object.keys(this.addSongInfo).length === 0) {
      //   this.$message({
      //     type: 'error',
      //     message: '请输入必填内容'
      //   })
      //   return;
      // }
      for (const key in this.addSongInfo) {
        if (!this.addSongInfo[key]) {
          return this.$message({
            type: 'error',
            message: '请输入必填内容'
          })
        }
      }

      // console.log(this.addSongInfo)
      Promise.ajax({
        "type": "post",
        "url": "https://meta-mock/songInfo/add",
        "data": JSON.stringify(this.addSongInfo)
      }).then(res => {
        // console.log(res.data);
        if (res.code == 200) {
          this.listSongInfo = res.data;
          this.$message({
            type: 'info',
            message: res.message
          })
        }

      })

    },
    clearItem: function () {
      this.addSongInfo = {
        name: "",
        lyric: "",
        power: ""
      };
    },
    removeItem: function (scope) {

      this.$confirm('是否执行删除操作?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        Promise.ajax({
          "type": "post",
          "url": "https://meta-mock/songInfo/remove",
          "data": scope.row.id
        }).then(res => {
          // console.log(res.data);
          if (res.code == 200) {
            this.listSongInfo = res.data;
            this.$message({
              type: 'info',
              message: res.message
            })
          }

        })

      }).catch(() => {

      })

    },
    editItem: function (scope) {
      scope.row.isEdit = true;
    },
    updateItem: function (scope) {
      scope.row.isEdit = false;

      Promise.ajax({
        "type": "post",
        "url": "https://meta-mock/songInfo/update",
        "data": JSON.stringify(scope.row)
      }).then(res => {
        // console.log(res.data);
        if (res.code == 200) {
          this.listSongInfo = res.data;
          this.$message({
            type: 'info',
            message: res.message
          })
        }
      })

    },
    detailItem: function () {
      // console.log(this.detailSongInfo)
      Promise.ajax({
        "type": "post",
        "url": "https://meta-mock/songInfo/detail",
        "data": JSON.stringify(this.detailSongInfo)
      }).then(res => {
        // console.log(res.data);
        if (res.code == 200) {
          this.listSongInfo = res.data;
          this.$message({
            type: 'info',
            message: res.message
          })
        }
      })
    }
  }


}
</script>

<style scoped>

</style>